.button {
  background-color: hsl(26, 100%, 55%);
  border: none;
  font-family: inherit;
  color: hsl(25, 100%, 94%);
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  width: 100%;
  height: 5rem;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.4s;
}

.button:hover {
  background-color: hsl(26, 80%, 50%);
  transition: all 0.4s;
}
@media screen and (max-width: 1050px) {
  .button {
    width: 100%;
    max-width: none;
  }
}
